@import "assets/sweet-portofolio";

/* Button Styling */
$primary-color: #0d6efd;
$primary-hover-color: #0b5ed7;
$white-color: #fff;

.btn-primary {
  background-color: $primary-color;
  border: none;
  color: $white-color;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;

  &:hover {
    background-color: $primary-hover-color;
  }
}

.btn-close {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
}

/* Modal Styling */
.modal {
  display: none;
  position: fixed;
  z-index: 1050;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-dialog {
  position: relative;
  width: auto;
  margin: 10px;
  pointer-events: none;
  max-width: 500px;
}

.modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: $white-color;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.3rem;
  outline: 0;
  padding: 1rem;
}

.modal-header,
.modal-body {
  padding: 1rem;
}

.modal-title {
  margin: 0;
  line-height: 1.5;
}

.list-group-item {
  display: block;
  width: 100%;
  padding: 10px 15px;
  margin-bottom: -1px;
  background-color: $white-color;
  border: 1px solid rgba(0, 0, 0, 0.125);

  &:hover {
    background-color: #f8f9fa;
  }
}

.badge {
  display: inline-block;
  padding: 0.25em 0.4em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.375rem;

  &.bg-danger {
    color: $white-color;
    background-color: #dc3545;
  }
}

.group-btn-portfolio {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  align-content: center;
  flex-wrap: nowrap;
}
.btn-preview-portfolio {
  color: #fff;
  background: #444;
  width: 50%;
  border-radius: 25px;
  text-align: center;
  padding: 9px 10px;
}
.btn-preview-portfolio:hover {
  color: #fff;
  background: #000;
}
.btn-whatsapp-portfolio {
  color: #fff;
  background: #25d366;
  width: 50%;
  border-radius: 25px;
  text-align: center;
  padding: 9px 10px;
}
.btn-whatsapp-portfolio:hover {
  color: #fff;
  background: #128c7e;
}
// Pagination styles
.pagination {
  display: flex;
  justify-content: center;
  padding-top: 3rem; // Adjust padding as needed

  .page-item {
    list-style: none;
    &.active {
      .page-link {
        background-color: #007bff;
        border-color: #007bff;
        color: #fff;
      }
    }

    .page-link {
      color: #007bff;
      padding: 0.5rem 0.75rem;
      border: 1px solid #dee2e6;
      border-radius: 0.25rem;
      text-decoration: none;
    }
  }
}

.frame-portofolio {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: flex-start;
  justify-content: space-between;
}

.col-portofolio {
  width: 50%;
  padding: 10px; /* Optional: Add padding for spacing */
}

/* For desktop and larger screens (4 columns) */
@media (min-width: 992px) {
  .col-portofolio {
    width: 33.33%;
  }
}

/* For tablet screens (3 columns) */
@media (min-width: 768px) and (max-width: 991px) {
  .col-portofolio {
    width: 50%;
  }
}

/* For mobile screens (1 column) */
@media (max-width: 767px) {
  .col-portofolio {
    width: 100%;
  }
}
